input {
  outline: none;
  border: none;
  box-shadow: none; }
  input:focus {
    outline: none; }

img {
  width: auto;
  max-width: 100%;
  height: auto; }

html,
body {
  max-width: 100%;
  overflow-x: hidden; }

body {
