      padding: 0 32px; } }
  @media (max-width: 767px) {
    .container.full-container {
      padding: 0 24px; } }

.icon {
  transition: all 0.3s;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
  flex-shrink: 0; }

.title {
