h2.title {
  font-size: 32px; }
  @media (max-width: 767px) {
    h2.title {
      font-size: 22px; } }

h3.title {
  font-size: 28px; }
  @media (max-width: 767px) {
    h3.title {
      font-size: 20px; } }

h4.title {
  font-size: 24px; }
  @media (max-width: 767px) {
    h4.title {
      font-size: 18px; } }

h5.title {
  font-size: 20px; }
  @media (max-width: 767px) {
    h5.title {
      font-size: 16px; } }

h6.title {
  font-size: 18px; }
  @media (max-width: 767px) {
    h6.title {
      font-size: 16px; } }

.body-paragraph {
  letter-spacing: -0.01em;
  line-height: 160%;
  color: var(--text-secondary-colour); }

.body-xl {
  font-size: 24px; }

.body-m {
  font-size: 18px; }

.body-s {
  font-size: 16px; }

.body-xs {
  font-size: 14px; }

.bold {
