.regular {
  font-weight: 400; }

.pb18 {
  padding-bottom: 18rem; }
  @media (max-width: 767px) {
    .pb18 {
      padding-bottom: 12rem; } }

@media (min-width: 1280px) {
  .container {
    max-width: 1240px;
    padding-right: 0;
    padding-left: 0; } }

@media (max-width: 767px) {
  .container {
    padding-right: 20px;
    padding-left: 20px; } }

