.header__menu {
  display: flex;
  align-items: center; }
  .header__menu .menu-list {
    display: flex;
    align-items: center; }
    .header__menu .menu-list::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 145%;
      height: 100%;
      background-color: var(--bg-primary-colour);
      z-index: -1;
      clip-path: polygon(0 0, 100% 0, 70% 100%, 0 100%);
      transform: translateX(-100%);
      transition: ease 0.4s; }
    @media (max-width: 767px) {
      .header__menu .menu-list {
        display: none; } }
    .header__menu .menu-list .menu-list__item:not(:last-child) {
      margin-right: 56px; }
    .header__menu .menu-list .menu-list__item .menu-list__link {
      font-weight: 700;
      font-size: 18px;
      line-height: 160%;
      color: var(--link-primary-colour);
      text-decoration: none;
      transition: all 0.3s;
      letter-spacing: -0.01em; }
      .header__menu .menu-list .menu-list__item .menu-list__link:hover {
        color: var(--link-primary-hover-colour); }
  @media (max-width: 767px) {
    .header__menu .menu-list.open-modal {
      display: flex;
      position: fixed;
      transition: all 0.3s;
      top: 0;
      left: 0;
      right: 0;
      z-index: 100;
      width: 100%;
      height: 100%;
      flex-direction: column;
      justify-content: center;
      text-align: center; }
      .header__menu .menu-list.open-modal::before {
        transform: none; }
      .header__menu .menu-list.open-modal .menu-list__item:not(:last-child) {
        margin-bottom: 4rem;
        margin-right: 0; } }
  .header__menu .hamburger {
    display: none;
    position: relative; }
    @media (max-width: 767px) {
      .header__menu .hamburger {
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        width: 30px;
        height: 15px;
        transform-origin: center center;
        transition: 0.5s ease-in-out;
        z-index: 101;
        position: relative; }
        .header__menu .hamburger::before {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          padding: 15px 10px;
          transform: translate(-20%, -30%); }
        .header__menu .hamburger span {
          width: 100%;
          height: 2px;
          background: var(--brand-primary-colour);
          transform: rotate(0deg);
          transition: 0.4s cubic-bezier(0.68, -0.6, 0.32, 1.6);
          cursor: pointer;
          position: absolute;
          left: 0;
          opacity: 1;
          width: 100%; }
          .header__menu .hamburger span:nth-child(1) {
            top: 0; }
          .header__menu .hamburger span:nth-child(2) {
            top: 6px;
            transform: translateX(-6px); }
          .header__menu .hamburger span:nth-child(3) {
            top: 12px; } }
  @media (max-width: 767px) {
    .header__menu .hamburger.open-modal::before {
      content: '';
      position: absolute;
      transform: translateY(-30%);
      padding: 15px 0;
      top: 0;
      right: 0;
      left: 0;
      bottom: 0; }
    .header__menu .hamburger.open-modal span {
      width: 20px;
      left: 20%; }
      .header__menu .hamburger.open-modal span:nth-child(1) {
        top: 0;
        left: 50%;
        opacity: 0; }
      .header__menu .hamburger.open-modal span:nth-child(2) {
        transform: rotate(45deg) translateX(0); }
      .header__menu .hamburger.open-modal span:nth-child(3) {
        top: 6px;
        transform: rotate(-45deg); } }
  .header__menu .site-sphere {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    margin-left: 56px;
    background: var(--brand-gradient-primary-colour); }
    @media (max-width: 1239px) {
      .header__menu .site-sphere {
        display: none; } }

